html {
  font-size: 62.5%;
  font-family: "Noto Sans JP", sans-serif;
}

* {
  box-sizing: border-box;
}

img {
  max-width: 100%;
}

a {
  color: #000;
  text-decoration: none;
}

ul, ol, h1, h2, h3, h4, h5, h6, p {
  margin: 0;
}

@media (min-width: 600px) {
  .pc-only {
    display: block;
  }
}
@media (max-width: 599px) {
  .pc-only {
    display: none;
  }
}

@media (min-width: 600px) {
  .sp-only {
    display: none;
  }
}
@media (max-width: 599px) {
  .sp-only {
    display: block;
  }
}

main {
  padding-top: 48px;
}

[data-btn=main] {
  width: 132px;
  margin: 0 auto;
}
[data-btn=main] a {
  width: 100%;
  display: inline-block;
  font-size: 1.8rem;
  color: #fff;
  line-height: 2em;
  text-align: center;
  border: 3px solid #009CCB;
  background-color: #009CCB;
  border-radius: 6px;
  transition: all 0.6s;
}
[data-btn=main] a:hover {
  color: #009CCB;
  background-color: #fff;
  transition: all 0.6s;
}

header {
  width: 100%;
  height: 48px;
  background-color: #009CCB;
  user-select: none;
  position: fixed;
  top: 0;
  z-index: 100;
}

.header-inner {
  max-width: 980px;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
}
.header-inner__logo {
  padding-left: 24px;
}
.header-inner__logo h1 a {
  color: #fff;
  font-size: 3rem;
  font-family: "Barlow", sans-serif;
  font-weight: 100;
  line-height: 48px;
}
.header-inner__nav ul {
  list-style: none;
  display: flex;
  height: 100%;
}
.header-inner__nav ul li {
  height: 100%;
}
.header-inner__nav ul li a {
  color: #fff;
  font-size: 1.6rem;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  width: 90px;
  height: 100%;
  line-height: 48px;
}
.header-inner__nav ul li a:hover {
  background-color: rgba(255, 255, 255, 0.4);
  transition: all 0.6s;
}
.header-inner__hamburger {
  position: relative;
  width: 50px;
  height: 48px;
  margin-right: 12px;
  background: none;
  border: none;
  appearance: none;
  cursor: pointer;
  transition: all 0.4s;
  text-align: center;
}
.header-inner__hamburger span {
  position: absolute;
  left: 12px;
  width: 27px;
  height: 3px;
  background-color: #fff;
  border-radius: 4px;
  display: inline-block;
  transition: all 0.4s;
}
.header-inner__hamburger span:nth-of-type(1) {
  top: 14px;
}
.header-inner__hamburger span:nth-of-type(2) {
  top: 23px;
}
.header-inner__hamburger span:nth-of-type(3) {
  bottom: 13px;
}
.header-inner__hamburger.on span:nth-of-type(1) {
  transform: translateY(9px) rotate(-45deg);
}
.header-inner__hamburger.on span:nth-of-type(2) {
  opacity: 0;
}
.header-inner__hamburger.on span:nth-of-type(3) {
  transform: translateY(-10px) rotate(45deg);
}

.header-modal {
  width: 100%;
  opacity: 0;
  visibility: hidden;
  background-color: #009CCB;
  transition: opacity 400ms;
  position: absolute;
  bottom: 0;
  left: 0;
  transform: translateY(100%);
}
.header-modal.on {
  opacity: 1;
  visibility: visible;
}
.header-modal__nav {
  margin: 0 24px;
  padding-top: 60px;
}
.header-modal__nav ul {
  list-style: none;
}
.header-modal__nav ul li {
  margin-bottom: 18px;
}
.header-modal__nav ul li a {
  font-size: 30px;
  color: #fff;
  font-weight: 700;
  display: inline-block;
  width: 100%;
}

footer {
  text-align: center;
  font-size: 1.2rem;
  padding: 60px 0 18px;
}

#front-page {
  width: 100vw;
}
#front-page .mv {
  width: 100%;
  height: 100vh;
  background: url("../img/common/img_thumb.png");
  user-select: none;
}
#front-page .blog__inner {
  max-width: 980px;
  margin: 60px auto 0;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}
#front-page .blog__inner::after {
  content: "";
  width: 300px;
}
@media (max-width: 1024px) and (min-width: 600px) {
  #front-page .blog__inner::after {
    width: 29.296875vw;
  }
}
@media (max-width: 599px) {
  #front-page .blog__inner::after {
    margin: 0 auto 30px;
  }
}
@media (max-width: 1024px) and (min-width: 600px) {
  #front-page .blog__inner {
    padding: 0 30px;
  }
}
#front-page .blog__inner .card {
  width: 300px;
  margin-bottom: 60px;
  border-radius: 30px;
  box-shadow: 0 0 6px rgba(0, 0, 0, 0.16);
}
@media (max-width: 1024px) and (min-width: 600px) {
  #front-page .blog__inner .card {
    width: 29.296875vw;
  }
}
@media (max-width: 599px) {
  #front-page .blog__inner .card {
    margin: 0 auto 30px;
  }
}
#front-page .blog__inner .card__thumb {
  height: 212px;
  border-radius: 30px 30px 0 0;
  overflow: hidden;
}
@media (max-width: 1024px) and (min-width: 600px) {
  #front-page .blog__inner .card__thumb {
    height: 20.703125vw;
  }
}
#front-page .blog__inner .card__thumb img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center;
  transition: all 0.6s;
}
#front-page .blog__inner .card:hover img {
  transform: scale(1.1, 1.1);
  transition: all 0.6s;
}
#front-page .blog__inner .card:hover .card__info {
  opacity: 0.6;
  transition: opacity 0.6s;
}
#front-page .blog__inner .card__info {
  padding: 18px;
  transition: opacity 0.6s;
}
#front-page .blog__inner .card__info .time-cat {
  display: flex;
  justify-content: space-between;
  font-size: 1.2rem;
}
#front-page .blog__inner .card__info .time-cat .time {
  color: #696969;
}
#front-page .blog__inner .card__info .time-cat .category {
  width: 132px;
  line-height: 18px;
  background-color: #009CCB;
  color: #fff;
  font-weight: 300;
  text-align: center;
}
@media (max-width: 1024px) and (min-width: 600px) {
  #front-page .blog__inner .card__info .time-cat .category {
    margin-left: 6px;
  }
}
#front-page .blog__inner .card__info .title {
  font-size: 1.8rem;
  line-height: 2.5rem;
  margin-top: 12px;
}
@media (min-width: 600px) {
  #front-page .blog__inner .card__info .title {
    min-height: 50px;
  }
}
#front-page .blog__inner .card__info .to-article {
  margin-top: 12px;
  color: #009CCB;
  font-size: 1.4rem;
  text-align: center;
}